import React, { useEffect, useState } from 'react';
import { DataGrid } from '@mui/x-data-grid';
import '../../App.css';

export default function AllProducts() {
  const [isLoading, setIsLoading] = useState(true);
  const [loadedProducts, setLoadedProducts] = useState([]);

  useEffect(() => {
    setIsLoading(true);
    fetch(
      'https://loureiro-5e5e1-default-rtdb.firebaseio.com/products.json'
    ).then(response => {
      return response.json();
    }).then(data => {
      const products = [];

      for(const key in data){
        const product = {
          id: key,
          ...data[key]
        };
        
        products.push(product);
      }

      setIsLoading(false);
      setLoadedProducts(products);
    });
  }, []);

  if (isLoading) {
    <section>
      <p>Loading... </p>
    </section>
  }

  const columns = [
    {
      field: 'nroArticulo',
      headerName: 'Nro Articulo',
      type: 'number',
      width: 90,
    },
    {
      field: 'proveedor',
      headerName: 'Proveedor',
      width: 300,
    },
    { field: 'descripcion', headerName: 'Descripcion', width: 400 },
    {
      field: 'precio',
      headerName: 'Precio',
      type: 'number',
      width: 100,
    },
    {
      field: 'oferta',
      headerName: 'Oferta',
      type: 'number',
      width: 70,
    },
    { field: 'iva', headerName: 'IVA', width: 30 },
    { field: 'fechaCambio', headerName: 'Fecha Cambio', width: 130 },
    {
      field: 'dolares',
      headerName: 'Dolares',
      width: 70,
    },
  ];

  return (
    <div className='table' style={{ height: 840, width: '100%' }}>
      <DataGrid
        rows={loadedProducts}
        columns={columns}
        pageSize={14}
        rowsPerPageOptions={[10]}
        checkboxSelection
      />
    </div>
  );
}