import React from 'react';
import './Cards.css';
import CardItem from './CardItem';

function Cards() {
  return (
    <div className='cards'>
      <h1>Novedades</h1>
      <div className='cards--container'>
        <div className='cards--wrapper'>
          <ul className='cards--items'>
            <CardItem
              src='images/img-9.jpg'
              text='NUEVO! Herramientas para outdoors importadas, modelo 2022'
              label='Motosierras'
              path='/products'
            />
            <CardItem
              src='images/img-2.jpg'
              text='Soplete de mano a gas'
              label='Sopletes'
              path='/products'
            />
          </ul>
          <ul className='cards--items'>
            <CardItem
              src='images/img-3.jpg'
              text='Agujereadora Black & Decker FS4281'
              label='Agujereadoras'
              path='/products'
            />
            <CardItem
              src='images/img-4.jpg'
              text='Kit de Palas Hoyeras'
              label='Herramientas Varias'
              path='/products'
            />
            <CardItem
              src='images/img-8.jpg'
              text='Cortadora de csped profesional'
              label='Outdoors'
              path='/products'
            />
          </ul>
        </div>
      </div>
    </div>
  );
}

export default Cards;
