import React from 'react'
import { Link } from 'react-router-dom'
import { Button } from './Button'
import './Footer.css';

function Footer() {
  return (
    <div className='footer-container'>
       <section className="footer-subscription">
           <p className="footer-subscription-heading">
               Suscribite para recibir nuestras ofertas y novedades!
           </p>
           <p className="footer-subscription-text">
               Te pods desuscribir en cualquier momento.
           </p>
           <div className="input-areas">
               <form>
                   <input type="email" name="email"  placeholder="Tu e-mail" className="footer-input" />
                   <Button buttonStyle='btn--outline'>Suscribirme</Button>
               </form>
           </div>
       </section>
       <div className="footer-links">
           <div className="footer-link-wrapper">
               <div className="footer-link-items">
                   <h2>Sobre Nosotros</h2>
                   <Link to='/'>Cmo trabajamos</Link>
                   <Link to='/'>Opiniones de clientes</Link>
                   <Link to='/'>Carreras</Link>
                   <Link to='/'>Trminos del servicio</Link>
               </div>
               <div className="footer-link-items">
                   <h2>Contactanos</h2>
                   <Link to='/'>Envianos un mensaje</Link>
                   <Link to='/'>Peticiones</Link>
                   <Link to='/'>Sugerencias</Link>
               </div>
           </div>
       </div>
       <section className="social-media">
           <div className="social-media-wrap">
               <div className="footer-logo">
                   <Link to='/' className="social-logo">
                       Hardware Store <i className="fab fa-typo3"></i>
                   </Link>
               </div>
               <small className="website-rights">Hardware Store  2022</small>
               <div className="social-icons">
                   <Link className="social-icon-link facebook" to='/' target='_blank' aria-label='Facebook'>
                       <i className="fab fa-facebook-f"></i>
                   </Link>
                   <Link className="social-icon-link instagram" to='/' target='_blank' aria-label='Instagram'>
                       <i className="fab fa-instagram"></i>
                   </Link>
                   <Link className="social-icon-link linkedin" to='/' target='_blank' aria-label='LinkedIn'>
                       <i className="fab fa-linkedin"></i>
                   </Link>
               </div>
           </div>
       </section>
    </div>
  )
}

export default Footer